.spinner-container {
    display: flex;
    justify-content: center;
    gap: 10px; /* Space between columns */
  }
  
  .number-column {
    width: 50px;
    height: 60px;
    overflow: hidden;
    border: 2px solid #333;
    border-radius: 8px;
    position: relative;
  }
  
  .number-list {
    display: flex;
    flex-direction: column;
    transition: transform 2s cubic-bezier(0.25, 1.5, 0.5, 1);
  }
  
  .number-list div {
    font-size: 2rem;
    text-align: center;
    height: 60px; /* Match container height */
    line-height: 60px;
  }
  
  button {
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 1rem;
    border: none;
    border-radius: 5px;
    background-color: #007bff;
    color: white;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #0056b3;
  }